<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2020-04-02 10:04:13
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-05-12 15:39:55
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Switch 开关</h3>
<hr>
<h4>按需引入使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Switch } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Switch.name, Switch)

</code></pre>
<h4>基础用法</h4>
<p><code v-pre>Switch</code>支持<code v-pre>default</code>、<code v-pre>success</code>、<code v-pre>warning</code>、<code v-pre>errer</code>四种类型，默认<code v-pre>default</code></p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> /&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> /&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> /&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"errer"</span> /&gt;</span>

</code></pre>
<h4>绑定值</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: <span class="hljs-literal">true</span>
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>自定义返回值</h4>
<p>通过<code v-pre>active-value</code>和<code v-pre>inactive-value</code>两个字段可以自定义选中和为选中的返回值</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">:active-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:inactive-value</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">@on-change</span>=<span class="hljs-string">"change"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    change(val) {
      <span class="hljs-keyword">this</span>.$Toast.info(<span class="hljs-string">`变化的值：<span class="hljs-subst">${val}</span>`</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">disabled</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">v-Switch</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: <span class="hljs-literal">true</span>
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Switch props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td><code v-pre>Switch</code>的类型</td>
<td>String</td>
<td><code v-pre>default</code> <code v-pre>errer</code> <code v-pre>success</code> <code v-pre>warning</code></td>
<td>default</td>
</tr>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>active-value</td>
<td><code v-pre>Switch</code> 打开时的值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>inactive-value</td>
<td><code v-pre>Switch</code> 关闭时的值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用<code v-pre>Switch</code></td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Switch Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>状态发生变化时的回调函数</td>
<td>新状态的值</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      value: true
    };
  }
};
</script>


